body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  margin: 0;
  color: #37393f;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  p {
    font-size: 1.6rem;
  }
}
@media (min-width: 1024px) {
  p {
    font-size: 1.8rem;
  }
}

.contenedor {
  width: 95%;
  max-width: 122rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #37393f;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  color: #37393f;
  font-weight: 300;
  text-align: center;
  margin: 0;
}

h1 {
  font-size: 3.2rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4.5rem;
  }
}
@media (min-width: 1024px) {
  h1 {
    font-size: 4.8rem;
  }
}

h2 {
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.2rem;
  }
}
@media (min-width: 1024px) {
  h2 {
    font-size: 4rem;
  }
}

h3 {
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 2.8rem;
  }
}
@media (min-width: 1024px) {
  h3 {
    font-size: 3.5rem;
  }
}

h4 {
  font-size: 2rem;
}
@media (min-width: 768px) {
  h4 {
    font-size: 2.4rem;
  }
}
@media (min-width: 1024px) {
  h4 {
    font-size: 3rem;
  }
}

.active-link {
  font-weight: 600;
  color: #6b78e5;
}

.btn-hero {
  padding: 0.84rem 1.4rem;
}
@media (min-width: 1024px) {
  .btn-hero {
    padding: 2.1rem 3.5rem;
  }
}

.btn-call {
  display: block;
  width: fit-content;
  padding: 1.8rem 2.7rem;
}
@media (min-width: 768px) {
  .btn-call {
    padding: 2.1rem 2.7rem;
  }
}

.btn-blue {
  background: #6B77E5;
  color: #ffffff;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: capitalize;
  transition: 0.3s box-shadow ease-in;
}
.btn-blue:hover {
  box-shadow: rgba(80, 63, 205, 0.5) 0 1px 10px;
}

.btn-green {
  background: #40ddb6;
  color: #37393f;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .btn-green {
    font-size: 1.8rem;
  }
}

.punter {
  width: 11px;
  height: 11px;
  background: #40ddb6;
}

.link-call-action {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: capitalize;
  position: relative;
}
@media (min-width: 1024px) {
  .link-call-action {
    font-size: 1.8rem;
  }
}
.link-call-action .line-link {
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  height: 0.4rem;
  border-radius: 10rem;
  background: #40ddb6;
  width: 0;
  transition: 0.3s width ease-out;
}
.link-call-action:hover .line-link {
  width: 100%;
}

.link-card {
  color: #6b78e5;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: capitalize;
}

.link-why {
  color: #40ddb6;
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: capitalize;
  position: relative;
}
.link-why .liner {
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  height: 0.4rem;
  border-radius: 10rem;
  background: #40ddb6;
  width: 0;
  transition: 0.3s width ease-out;
}
.link-why:hover .liner {
  width: 100%;
}

.scroll-none {
  overflow: hidden;
}

.container-cabe {
  display: none;
  background: #37393f;
  padding: 1.2rem 1.6rem;
}
@media (min-width: 768px) {
  .container-cabe {
    padding: 2.1rem 0;
    display: flex;
  }
}
@media (min-width: 1024px) {
  .container-cabe {
    height: 6rem;
  }
}

.list-cabe {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .list-cabe {
    flex-direction: row;
    justify-content: space-between;
    width: 95%;
    max-width: 122rem;
    margin: 0 auto;
  }
}
.list-cabe .list-1 {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .list-cabe .list-1 {
    justify-content: start;
    gap: 4.2rem;
  }
}
.list-cabe .item-clock {
  display: flex;
  justify-content: center;
}
.list-cabe li {
  display: flex;
  align-items: center;
  gap: 0.9rem;
}
.list-cabe li p,
.list-cabe li a {
  color: #ffffff;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.list-cabe li svg {
  fill: #ffffff;
  width: 2rem;
}

.container-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  height: 7.4rem;
  padding: 1.6rem 0;
  width: auto;
  max-width: 100%;
  margin: 0;
}
@media (min-width: 768px) {
  .container-nav {
    padding: 0;
  }
}
@media (min-width: 1024px) {
  .container-nav {
    padding: 3.6rem 0;
    height: 13.1rem;
    width: 95%;
    max-width: 122rem;
    margin: 0 auto;
  }
}

.logo-nav {
  width: 4.2rem;
  height: 4.2rem;
  margin-left: 1.6rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 1024px) {
  .logo-nav {
    margin: 0;
  }
}
.logo-nav p {
  display: none;
  font-family: "Poppins", sans-serif;
  font-size: 2.5rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.logo-nav p span {
  color: #6b78e5;
}
@media (min-width: 768px) {
  .logo-nav p {
    display: flex;
  }
}

.container-nav-list {
  display: flex;
  gap: 4rem;
}

.list-nav {
  z-index: 5;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 7.4rem 0;
  margin: 0;
  list-style: none;
  align-items: center;
  position: absolute;
  background: #ffffff;
  width: 80%;
  height: 1000vh;
  top: 0;
  right: -100%;
  position: fixed;
  border-radius: 1rem 0 0 1rem;
  transition: 0.3s right ease-out;
}
@media (min-width: 768px) {
  .list-nav {
    padding: 8.6rem 0;
    top: 6.7rem;
  }
}
@media (min-width: 1024px) {
  .list-nav {
    position: relative;
    right: 0;
    top: 0;
    width: auto;
    height: auto;
    flex-direction: row;
    gap: 4.2rem;
    padding: 0;
  }
}
.list-nav .item-link {
  position: relative;
}
.list-nav .line {
  position: absolute;
  height: 0.4rem;
  border-radius: 10rem;
  background: #6b78e5;
  width: 0;
  left: 0;
  bottom: -0.05rem;
  transition: 0.3s width ease-in;
}
.list-nav .link {
  font-size: 1.8rem;
  transition: 0.3s color ease-in;
}
.list-nav .link:hover {
  color: #6b78e5;
}
.list-nav .link:hover .line {
  width: 100%;
}
.list-nav .active-link .line {
  width: 100%;
}
.list-nav .btn-x {
  position: absolute;
  top: 2.5rem;
  right: 1.6rem;
}
@media (min-width: 1024px) {
  .list-nav .btn-x {
    display: none;
  }
}

.mostrar {
  right: 0;
}

.btn-bar {
  margin-right: 1.6rem;
  border: none;
  background: transparent;
  padding: 0;
}
@media (min-width: 1024px) {
  .btn-bar {
    margin-right: 0;
    display: none;
  }
}

.hero {
  background: url(../img/hero.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 1024px) {
  .hero {
    height: 81.5rem;
  }
}

.container-info-hero {
  background: rgba(55, 57, 63, 0.9098039216);
  backdrop-filter: blur(9.5px);
  display: flex;
  align-items: center;
  padding: 7rem 1.6rem;
}
@media (min-width: 768px) {
  .container-info-hero {
    padding: 12rem 2rem;
  }
}
@media (min-width: 1024px) {
  .container-info-hero {
    width: 43%;
    height: 100%;
    padding: 0 11.6rem;
  }
}
.container-info-hero .info-hero p,
.container-info-hero .info-hero h1 {
  color: #ffffff;
}
.container-info-hero .info-hero .sub-title {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  margin-bottom: 1rem;
}
.container-info-hero .info-hero .sub-title p {
  font-size: 1.6rem;
}
.container-info-hero .info-hero .title {
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-align: left;
  margin-bottom: 2rem;
}
.container-info-hero .info-hero .title span {
  color: #40ddb6;
}
.container-info-hero .info-hero .description {
  width: 30rem;
  color: #B9B9B9;
  margin-bottom: 3.4rem;
}
@media (min-width: 768px) {
  .container-info-hero .info-hero .description {
    width: 60%;
  }
}
@media (min-width: 1024px) {
  .container-info-hero .info-hero .description {
    width: 90%;
  }
}

@media (min-width: 768px) {
  .presentation {
    background: linear-gradient(to right, #f4f4f4 30%, transparent 30%);
  }
}
@media (min-width: 1024px) {
  .presentation {
    background: linear-gradient(to right, #f4f4f4 43%, transparent 43%);
  }
}

.container-presentation {
  display: flex;
  flex-direction: column-reverse;
  margin-top: 3rem;
  gap: 3rem;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .container-presentation {
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 4.5rem 2rem;
  }
}
@media (min-width: 1024px) {
  .container-presentation {
    width: 95%;
    max-width: 122rem;
    margin: 0 auto;
    gap: 12.1rem;
    padding: 9.6rem 0;
  }
}

.container-presentation-call {
  background: #f4f4f4;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .container-presentation-call {
    padding: 0;
    background: none;
  }
}

.call-info img {
  height: 19.5rem;
