  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .call-info img {
    height: 35rem;
    object-position: 0rem 0;
  }
}
@media (min-width: 1024px) {
  .call-info img {
    width: 408px;
    height: 446px;
  }
}
.call-info .info {
  padding: 0 1.6rem;
}
@media (min-width: 768px) {
  .call-info .info {
    padding: 0;
  }
}
.call-info .info h4 {
  font-style: normal;
