  margin-bottom: 1.3rem;
}
@media (min-width: 1024px) {
  .call-info .info p {
    margin-bottom: 3rem;
  }
}

.container-presentation-info {
  padding: 0 1.6rem;
}
@media (min-width: 1024px) {
  .container-presentation-info {
    padding: 0;
  }
}
.container-presentation-info .container-description-presentation {
  display: flex;
  flex-direction: column;
