}
.list-description .item-description p {
  font-size: 1.6rem;
}

.container-experience {
  width: fit-content;
  padding: 2.8rem 3rem;
  display: flex;
  gap: 1.9rem;
  border: 0.1px solid #E3E3E3;
  margin-bottom: 3.7rem;
}
@media (min-width: 1024px) {
  .container-experience {
    padding: 3.1rem 3.2rem;
  }
}
.container-experience .experience-info span {
  font-size: 2rem;
  font-style: normal;
  font-weight: 600;
